import { Project } from '../../project';
import { Application } from '../index';
import { Pipeline } from './index';

export enum PipelineWorkflowStatus {
  Canceled = 'Canceled',
  Completed = 'Completed',
  Failed = 'Failed',
  Running = 'Running',
  Queued = 'Queued',
  Waiting = 'Waiting',
}

export enum PipelineStepStatus {
  Canceled = 'Canceled',
  Completed = 'Completed',
  Failed = 'Failed',
  Running = 'Running',
  Queued = 'Queued',
  Waiting = 'Waiting',
}

export type PipelineStepOutputLine = {
  id: string;
  text: string;
  PipelineStepOutputId: PipelineStepOutput['id'];
};

export type PipelineStepOutput = {
  id: string;
  PipeWorkflowStepId: PipelineWorkflowStep['id'];
};

export type PipelineWorkflowStep = {
  enabled: boolean;
  id: string;
  status: PipelineStepStatus;
  PipeWorkflowId: PipelineWorkflow['id'];
};

export type PipelineWorkflow = {
  description: string;
  enabled: boolean;
  id: string;
  name: string;
  ActivePipelineId: Pipeline['id'];
  ApplicationId: Application['id'];
  ProjectId: Project['id'];
};
