import React from 'react';

export enum PageRole {
  ForgotPassword = 'forgot_password',
  Index = 'index',
  Login = 'login',
  PageNotFound = '404',
  Signup = 'signup',
  VerifyCode = 'verify_code',
}

export type PageRoute = {
  loginRequired?: boolean;
  path?: string;
  component: React.ElementType;
  role?: PageRole;
};
