import { DateTime } from 'luxon';

export enum TaskStatus {
  'Canceled' = 'Canceled',
  'Completed' = 'Completed',
  'Created' = 'Created',
  'Faulted' = 'Faulted',
  'Queued' = 'Queued',
  'Running' = 'Running',
  'Waiting' = 'Waiting',
}

export type Task = {
  created?: DateTime;
  data: Record<string, string> | Map<string, string>;
  name: string;
  id?: string;
  status?: TaskStatus;
};
